import { mapMutations, mapState } from 'vuex';
export default {
	data() {
		return {
			isWXAppInstalled: true
		};
	},
	computed: {
		...mapState(['shareMsg'])
	},
	onLoad() {
		let platform = uni.getSystemInfoSync().platform;






	},
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/index?server=' + user.server_people_id
			};
		}
	},
	methods: {
		...mapMutations(['setLogin', 'setWxInfo']),
		onSubmit(e) {
			let that = this;
			let data=e.detail.value;
			console.log('提交数据：', e.detail.value);
			for(let key in data){
				if(!data[key]){
					uni.showToast({
						title:"用户名或密码不能为空！",
						icon:"none"
					})
					return false
				}
			}
			this.$Fetch({
				url: 'login/doit',
				data:data,
				success: res => {
					uni.setStorage({
						key: 'TOKEN',
						data: res,
						success: function() {
							that.setLogin();
							uni.showToast({
								title: '登录成功',
								icon: 'success'
							});
							if (that.shareMsg.path) {
								setTimeout(() => {
									uni.reLaunch({
										url: that.shareMsg.path
									});
								}, 1500);
							} else {
								setTimeout(() => {
									uni.reLaunch({
										url: '/pages/index/index'
									});
								}, 1500);
							}
						}
					});
				}
			});
		},
		wxLogin() {
			let that = this;
			uni.login({
				provider: 'weixin',
				success: loginRes => {
					// console.log(loginRes.authResult);
					uni.getUserInfo({
						provider: 'weixin',
						lang: 'zh_CN',
						success: infoRes => {
							// console.log(infoRes.userInfo);
							// console.log('用户昵称为：' + infoRes.userInfo.nickName);
							that.setWxInfo(infoRes.userInfo);
							that.$Fetch({
								url: 'login/wxLogin',
								data: {open_id: infoRes.userInfo.unionid },
								debug: true,
								success: res => {
									uni.setStorage({
										key: 'TOKEN',
										data: res,
										success: function() {
											that.setLogin();
											uni.showToast({
												title: '登录成功',
												icon: 'success'
											});
											setTimeout(() => {
												uni.reLaunch({
													url: '/pages/index/index'
												});
											}, 1500);
										}
									});
								}
							});
						}
					});
				}
			});
		},
		xiaoLogin(){
			uni.showLoading({
				mask:true,
				title:"登录中···"
			})
			let that=this;
			wx.getSetting({
			  success(res) {
			    if (!res.authSetting['scope.userInfo']) {
			      uni.navigateTo({
			      	url:"/pages/authorize/authorize"
			      })
			    }else{
					wx.login({
						success: res => {
							console.log('微信登录', res);
							wx.getUserInfo({
								withCredentials:true,
								success:ret=>{
									that.$Fetch({
										url: 'login/xiaoLogin',
										data: {
											js_code: res.code,
											user_name: ret.userInfo.nickName,
											head_img: ret.userInfo.avatarUrl,
											encryptedData: ret.encryptedData,
											iv: ret.iv,
											server_people_id:that.shareMsg.server
										},
										debug: true,
										success: res => {
											console.log(res);
											uni.setStorage({
												key: 'TOKEN',
												data: res,
												success: function() {
													that.setLogin();
													uni.showToast({
														title: '登录成功',
														icon: 'success'
													});
													if (that.shareMsg.path) {
														setTimeout(() => {
															uni.reLaunch({
																url: that.shareMsg.path
															});
														}, 1500);
													} else {
														setTimeout(() => {
															uni.reLaunch({
																url: '/pages/index/index'
															});
														}, 1500);
													}
												}
											});
										}
									});
								}
							})
						}
					});
				}
			  }
			})
		}
	}
};
